.steps .ball {
  position: relative; }
  .steps .ball::after {
    position: absolute;
    z-index: 2;
    top: 50%;
    transform: translate(-50%, -50%);
    left: 135%;
    content: '';
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-image: linear-gradient(180deg, #EB9C37 0%, #DD4C57 100%); }
    @media (max-width: 768px) {
      .steps .ball::after {
        display: none; } }
  .steps .ball::before {
    position: absolute;
    z-index: 2;
    top: 100%;
    transform: translate(-50%, -50%);
    left: 135%;
    content: '';
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-image: linear-gradient(180deg, #EB9C37 0%, #DD4C57 100%); }
    @media (max-width: 768px) {
      .steps .ball::before {
        display: none; } }

